@import url(https://fonts.googleapis.com/css?family=Comfortaa|Exo+2|Raleway|Ubuntu);
/* Color Variables */

:root {
    --one: #e24e42;
    --two: #D9B310;
    --three: #EB6E80;
    --four: #008F95;
    --five: #2B2A2A;
}

/* Fonts and Imports */
body {
    background-color: rgb(255, 255, 255);
    background-image: url(/coding_challenge-13/static/media/parallax.4cbbd78c.svg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}


body::-webkit-scrollbar {
    width: 15px;
}

body::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
    -webkit-box-shadow: inset 0 0 6px rgba(231, 47, 1, 0.651);
}

body::-webkit-scrollbar-thumb {
    background-color: #e24e42;
    background-color: var(--one);
    outline: 1px solid #e24e42;
    outline: 1px solid var(--one);
}
.collapse.show {
    background: #fff
}

.title {
    font-family: 'Raleway', sans-serif;
    margin-bottom: 25px;
}

.button-primary {
    color: #e24e42;
    color: var(--one);
    padding: 8px 30px;
    border-radius: 15px;
    border: 1px solid #e24e42;
    border: 1px solid var(--one);
    margin: 0 10px;
    background: transparent;
    cursor: pointer !important;
}

.button-primary:focus {
    outline: 0;
}

.button-primary:hover,
.button-primary.active {
    background-color: #EB6E80;
    background-color: var(--three);
    border-color: #EB6E80;
    border-color: var(--three);
    color: white;
    text-decoration: none;
}

.trends {
    width: 80vw;
    margin: 30px auto;
    padding: 50px 30px;
    text-align: center;
    background: #f0d6c886;
    border: 1px solid #fff;
    border-radius: 30px
}

.chart-container {
    position: relative;
    width: 95%;
    margin: 0 auto auto auto;
}

h2 {
    color: #e24e42;
    color: var(--one);
    text-decoration: underline;
}

.svg-flip {
    position: relative;
    top: -1px;
    -webkit-transform: rotate(180deg);
    /* Chrome and other webkit browsers */
    /* FF */
    /* Opera */
    /* IE9 */
    transform: rotate(180deg);
    z-index: -1;
}
.nav-buttons {
    margin-left: auto;
    padding: 15px;
}

#header a {
    color: var(--three);
    margin-bottom: 7px;
}

#header .active {
    color: #fff
}

#header a:hover {
    color: #fff
}

.logo-active{
    -webkit-transition: height 0.3s;
    transition: height 0.3s;
}

.scroll {
    background: rgba(255, 255, 255, 1);
    height: 65px
}

.smLogo > a > img {
    height: 40px;
    position: relative;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.scroll > .nav-buttons {
    position: relative;
}


  @media only screen and (max-width: 800px) {
    #header {
        background: #fff
    }
}
.center {
    display: flex;
    justify-content: center;
    margin: 125px auto 0 auto
}

.center > div  {
    width: 25%;
}

.center > div > button {
    width: 80%;
    background: rgba(255, 255, 255, 0.219)
}


.lang-links {
    cursor: pointer;
}

.rank {
    border: 2px solid #e24e42;
    border-radius: 30px;
    padding-top: 50px;
    text-decoration: underline;
    color: #e24e42;
}

#newsletter .content {
    background: #FFFFFF;
    padding: 50px
}

.ml-form-embedWrapper {
    width: 70vw!important;
}

#mlb2-1154434.ml-form-embedContainer .ml-form-embedWrapper {
    background: transparent!important;
}

.ml-form-embedContent h4 {
    font-family: 'Ubuntu', sans-serif!important;
    color: var(--five)!important;
    font-size: 2em!important;
    text-align: center!important;
}

.ml-form-embedContent p {
    color: rgb(107, 107, 107)!important;
    text-align: center!important;
    margin-bottom: 30px!important
}
.container-grid {
    display: grid;
    width: 45%;
    grid-gap: 10px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 100px);
}

.wrapper-data {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-items: center;
}

.zone > img {
    width: 60px;
    height: auto;
    margin-bottom: 230px;
    align-content: center
}

.more {
    font-size: 4rem;
    color: #fff;
    font-family: Helvetica;
}

.arrow2 {
    display: none;
}

.source-chart {
    margin-top: 40px;
}

@media only screen and (max-width: 1110px) {
  .arrow {
    display: none;
  }
  .arrow2 {
    display: flex;
  }
  .wrapper-data {
    flex-direction: column;
  }
  .container-grid {
    width: auto;
  }
  .source-chart {
    margin-top: 0;
  }
}

/* IPhone 5/SE */
@media only screen and (max-width: 320px), (max-height: 320px) {
  .arrow2 {
    display: none;
  }
  .container-grid .tooltip1:hover .top {
    display: none;
  }
  .trends {
    width: 96vw;  
  }
  .container-grid {
    width: auto;
    grid-gap: 1px;
    grid-template-columns: repeat(4, 70px);
  }
  .source-chart {
    width: auto;
  }
  .more {
    font-size: 3.8rem;
    color: #fff;
  }
  .mb-4 {
    margin: 1rem;
  }
} 

/* Other devices */
@media only screen and (max-width: 414px), (max-height: 414px) {
  .arrow2 {
    display: none;
  }
  .mb-4 {
    margin: 1rem;
  }
}

div > span {
    font-size: 10rem;
    color: #fff;
}

.zone {
    text-align: center;
    font-size:10em;
    border-radius:4px;
    border:1px solid #bbb;
}

/***********************************************************************
 *  Purple Background
 **********************************************************************/
.purple {
    background: #A88BEB; /* Old browsers */ /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#A88BEB), color-stop(44%,#F1A7F1), color-stop(100%,#F8CEEC)); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(to bottom, #A88BEB 0%,#F1A7F1 44%,#F8CEEC 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
    background: -webkit-linear-gradient(top, #A88BEB 0%,#F1A7F1 44%,#F8CEEC 100%);
    background: linear-gradient(to bottom, #A88BEB 0%,#F1A7F1 44%,#F8CEEC 100%); /* W3C */
}

.tooltip1 {
    display:inline-block;
    position:relative;
    text-align:left;
}

.tooltip1 .top {
    min-width:200px;
    top:-20px;
    left:50%;
    -webkit-transform:translate(-50%, -100%);
            transform:translate(-50%, -100%);
    padding:10px 0 5px 0;
    color:var(--three);
    background-color:rgba(255, 255, 255, 0.95);
    font-weight:normal;
    font-size:13px;
    border-radius:8px;
    position:absolute;
    z-index:99999999;
    box-sizing:border-box;
    box-shadow:0 1px 8px rgba(0,0,0,0.5);
    display:none;
    border: 1px solid var(--three);
    text-transform: uppercase
}

.tooltip1:hover .top {
    display:block;
}

.tooltip1 .top i {
    position:absolute;
    top:100%;
    left:50%;
    margin-left:-12px;
    width:24px;
    height:12px;
    overflow:hidden;
}

.tooltip1 .top i::after {
    content:'';
    position:absolute;
    width:12px;
    height:12px;
    left:50%;
    -webkit-transform:translate(-50%,-50%) rotate(45deg);
            transform:translate(-50%,-50%) rotate(45deg);
    background-color:var(--three);
    box-shadow:0 1px 8px rgba(0,0,0,0.5);
}

.footer {
    background: #201e1c;
}

footer .socials i {
    padding: 10px 20px 0 0;
    font-size: 40px
}

footer .socials i:hover {
    color: var(--three)
}

.credits h5 {
    margin-top: 20px;
    color: #8d8d8d;
    font-family: 'Ubuntu', sans-serif;
}
.credits li {
    list-style-type: none;
    color: #fff
}
.credits li:hover {
    color: var(--one);
}
.credits a {
    text-decoration: none;
}

.copyright {
    background-color: #0e0f0f;
    color: #8d8d8d;
    text-align: center;
    padding: 20px
}

.fab {
    color: #ffffff;
}


@media only screen and (max-width: 800px) {
    .footer {
        padding: 0 30px;
        text-align: center
    }
    .footer a {
        margin-top: 30px
    }
    .credits {
        margin: 30px auto 0px auto
    }
    .credits ul {
        padding: 0!important
    }
  }
  @media only screen and (min-width: 767px) {
    .footer .credits {
        text-align: right
    }

}

.cFooterlink:hover {
    cursor: pointer;
}

.contribCard {
    border-radius: 5px 5px 10px 10px;
    border: 1px solid rgba(80, 80, 80, 0.322);
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.664);
}

.contribCard:hover {
    box-shadow: 4px 4px 16px 0px rgba(0, 0, 0, 0.22);
}

.contribCard>p {
    color: rgba(97, 97, 97, 0.589);
}

.contribCard h5 {
    color: var(--three);
    text-transform: uppercase;
}

.contribCard img {
    border-radius: 5px 5px 0 0;
    width: 100%;
}

.modal-header h5 {
    margin: 0px;
}

.modal-body {
    background: url(/coding_challenge-13/static/media/parallax.4cbbd78c.svg);
    margin: 0;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
